.body {
  position: relative;
  overflow: hidden;
  height: 100vh;
  width: 100vw;
}

.panel {
  position: absolute;
  top: 43%;
  left: 50%;
  transform: translate(-50%, -50%);
  margin-top: -10px;
  /* background-color: #fafafa; */
  width: 520px;
  /* border-radius: 10px;
   padding-top: 30px;
  padding-bottom: 30px; */
}

.signInPanel {
  background-color: #fafafa;
  border-radius: 10px;
  padding-top: 30px;
  padding-bottom: 30px;
}

.virtualAiLogo {
  height: 100px;
  width: auto;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.title {
  color: #6d6d73;
  font-size: 30px;
  line-height: 36px;
}

.description {
  font-weight: 400;
  font-size: 15px;
  line-height: 17.9px;
  letter-spacing: 3%;
  width: 390px;
  margin-top: 30px;
}

.emailInput,
.passwordInput {
  font-size: 14px;
  line-height: 16.71px;
  font-weight: 400;
  color: #6d6d73;
  background: #fafafa;
  height: 33px;
  padding: 8px 15px 8px 5px;
  margin-top: 30px;
  box-shadow: inset 0px -1px 0px #6d6d73;
  border: none;
  outline: none;
}

.emailInput {
  margin-top: 30px !important;
  margin-left: -6px;
  width: 389px !important;
}

.passwordInput {
  margin-top: 20px;
  margin-left: 50px;
  width: 390px;
}

.eyeIcon {
  position: absolute;
  margin-top: 25px;
  margin-right: 70px;
  right: 0;
}

.errorAlertText {
  font-weight: 400;
  font-size: 12px;
  color: #ff7878;
  margin-top: 45px !important;
}

.signInButton {
  font-size: 18px;
  font-weight: 100;
  line-height: 21px;
  color: #ffffff;
  background-color: #008662;
  width: 390px !important;
  height: 43px;
  padding: 11px 24px;
  margin-top: 70px !important;
  /* margin-left: 63px; */
  border: none;
  border-radius: 4px;
}

.signInButton:disabled {
  opacity: 0.8;
}

.signInIcon {
  filter: brightness(0) invert(1);
  margin-left: 8px;
}

.marginTop0 {
  margin-top: 0px !important;
}

.redirectUrlText {
  font-size: 15px;
  line-height: 18px;
  letter-spacing: 0.03em;
  color: #6d6d73;
  margin-top: 15px !important;
}

.credentialList {
  position: absolute;
  background-color: #eaeaea;
  border-radius: 4px;
  width: 389px;
  margin-left: 50px;
  margin-top: 5px;
  z-index: 1;
  max-height: 100px;
  overflow-y: auto;
}

.credentialListItem {
  cursor: pointer;
  height: 25px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow-x: hidden;
  font-size: 14px;
  text-align: left;
  padding-left: 10px;
  padding-right: 10px;
}

.credentialListItem:hover {
  background-color: #d6d5d5;
}
