.checkboxDiv {
  display: flex;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.box {
  border-radius: 4px;
}

.circle {
  border-radius: 50%;
}

.box,
.circle {
  border: 1px solid #008662;
  width: 17px;
  height: 17px;
  position: relative;
  cursor: pointer;
}

.notChecked {
  background-color: #ffffff;
}

.checked {
  background-color: #008662;
}

.checkIcon {
  position: absolute;
  content: url('../../assets/icons/check.svg');
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  margin: auto;
}

.label {
  color: #343a40 !important;
  font-size: 14px;
  line-height: 18px;
  margin-left: 5px;
}

.disabled {
  pointer-events: none;
}

.disabledNotChecked {
  border: 1px solid #b1b1b1;
}

.disabledChecked {
  background-color: #b1b1b1;
  border: 1px solid #b1b1b1;
}

.labelRight {
  color: #343a40;
  font-size: 14px;
  line-height: 18px;
  margin-right: 5px;
}

.labelBold {
  font-weight: bolder;
}

.labelDisabled {
  color: #c6c6c6;
}
